namespace $ { export class $mol_float extends $mol_view {

	/// shiftStyle \
	shiftStyle() {
		return ""
	}

	/// style * transform <= shiftStyle
	style() {
		return $mol_merge_dict( super.style() , {
			"transform" : ()=> <any> this.shiftStyle() ,
		} )
	}

	/// scrolling false
	scrolling() {
		return false
	}

	/// attr * mol_float_scrolling <= scrolling
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_float_scrolling" : ()=> <any> this.scrolling() ,
		} )
	}

} }

